@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.timer_result_wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }

  .tomato_icons{
    text-align: center;
  }
  
  .tomato_icon{
    font-size: 3rem;
    color: #fff;
    margin: 0 0.5rem;
  }
  
  .timer_text p {
    font-size: 1.6rem;
    color: #fff;
    text-align: center;
    padding: 2.5rem 0;
  }

  .icons{
    text-align: center;
  }

  .play_icon, .pause_icon{
    font-size: 6rem;
    color: #fff;
    cursor: pointer;
  }
.progress_bar{
    width: 70%;
    max-width: 500px;
    height: 15px;
    background-color: #fff;
    border-radius: 8px;
}

.timer_wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.clock{
    font-size: 7rem;
    color: #fff;
    padding: 5rem 0;
    letter-spacing: 2px;
  }

  .time p:nth-child(1){
    font-size: 1.6rem;
    color: #fff;
    text-align: center;
  }
  
.info_wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
  
.info_icon, .close_btn{
    font-size: 4rem;
    color: #fff;
    cursor: pointer;
}

.info_area{
    display: none;
}

.active_info{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90%;
    height: 90%;
    border-radius: 18px;
    background-color: #fa9698;
    overflow: hidden;
}

.close_btn{
    position: absolute;
    top: 0;
    right: 0;
    margin: 5vw;
    cursor: pointer;
}

.pomodoro_info_h1{
    font-size: 2.5rem;
    line-height: 150%;
    color: white;
    text-align: center;
    margin: 1rem;
}

.pomodoro_info_text{
    font-size: 1.6rem;
    line-height: 150%;
    margin: 1rem;
    color: #a80c0f;
    text-align: center;
}

