body{
  background-color: #F04042;
  font-family: 'Montserrat', sans-serif;
}

.wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100vh;
  max-width: 1440px;
  margin: 0 auto;
  padding: 2.5rem 2rem;
}




